<template>

<div class="section video">
  <div id="videoTitle">
    <SectionTitle :title="heading"></SectionTitle>
  </div>
  <div class="videos">
    <div id="leftVideo" class="vidBox">
      <font-awesome-icon icon="fa-solid fa-circle-play" v-on:click="playVideo( $event, 'thumbnailLeft', 'leftIframe')" />
      <img id="thumbnailLeft" :src="videoLeft.thumbnail" />
      <iframe id="leftIframe" :title="videoLeft.title" :src="videoLeft.src" :allow="videoLeft.allow"></iframe>
      <div class="videoDescriptionWrapper">
        <div class="videoTitle">{{ videoLeft.title }}</div>
        <hr>
        <div class="videoSubtitle">{{ videoLeft.subtitle }}</div>
      </div>
    </div>
    <div id="rightVideo" class="vidBox">
      <font-awesome-icon icon="fa-solid fa-circle-play" v-on:click="playVideo($event, 'thumbnailRight', 'rightIframe')" />
      <img id="thumbnailRight" :src="videoRight.thumbnail" />
      <iframe id="rightIframe" :title="videoRight.title" :src="videoRight.src" :allow="videoRight.allow"></iframe>
      <div class="videoDescriptionWrapper">
        <div class="videoTitle">{{ videoRight.title }}</div>
        <hr>
        <div class="videoSubtitle">{{ videoRight.subtitle }}</div>
      </div>
    </div>
  </div>
  <div id="videoButton">
    <MainButton :link="buttonLink" :button-text="buttonText"></MainButton>
  </div>
</div>

</template>

<script>

import SectionTitle from "@/components/titles/SectionTitle";
import MainButton from "@/components/buttons/MainButton";

export default {
  name: "SectionVideo",
  components: {
    MainButton,
    SectionTitle
  },
  data() {
    return {
      heading: "Video",
      buttonText: "Mehr Filme",
      buttonLink: "/hochzeitsvideografie",
      videoLeft: {
        title: "Wedding Trailer Left",
        subtitle: "Some Description",
        src: "https://player.vimeo.com/video/426412008?h=5fb3d9c08f&sidedock=0&title=0&loop=1&muted=0&byline=0&portrait=0&autopause=0&app_id=122963",
        allow: "autoplay; fullscreen; picture-in-picture;",
        thumbnail: "/img/beatriz-perez-moya-unsplash.jpg",
      },
      videoRight: {
        title: "Wedding Trailer Right",
        subtitle: "Some Description",
        src: "https://player.vimeo.com/video/401522694?h=cd208f189d&sidedock=0&title=0&loop=1&muted=0&byline=0&portrait=0&autopause=0&app_id=122963",
        allow: "autoplay; fullscreen; picture-in-picture;",
        thumbnail: "/img/sandy-millar-unsplash.jpg",
      }
    }
  },
  methods: {
    playVideo: function(event, imgID, iframeID) {
      event.target.style.display = "none";
      document.getElementById(imgID).style.display = "none";
      let iframe = document.getElementById(iframeID);
      // eslint-disable-next-line no-undef
      let player = new Vimeo.Player(iframe);
      player.play();
    }
  }
}

</script>

<style scoped>

.video {
  margin: 0;
}

#videoTitle {
  margin-bottom: 20px;
}

.videos {
  width: 90vw;
  margin: 50px auto 0 auto;
  display: flex;
  justify-content: center;
}

.vidBox {
  position: relative;
  width: 40vw;
  max-width: 640px;
  margin: 0 auto;
}

.vidBox:before {
  content: "";
  display: block;
  padding-top: 56%; /* 16:9 format */
}

.vidBox iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;;
  left: 0;
  right: 0;
  bottom: 0;
  border: 0;
}

.vidBox img {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  object-fit: cover;
  max-width: 640px;
  max-height: 360px;
  z-index: 15;
}

iframe {
  width: 100%;
  margin: 0 auto;
}

svg {
  position: absolute;
  margin: 0 auto;
  width: 100%;
  height: 20%;
  top: 40%;
  color: white;
  cursor: pointer;
  z-index: 20;
}

.videoDescriptionWrapper {
  width: 100%;
  margin: 0 auto;
  position: absolute;
  padding-top: 30px;
  text-align: center;
}

.videoTitle {
  font-size: 2vw;
}

.videoSubtitle {
  font-size: 1.5vw;
}

hr {
  width: 50%;
}

#videoButton {
  margin: 130px 0 50px 0;
}

@media screen and (max-width: 1080px) {

  .vidBox {
    max-width: 1080px;
    width: 90vw;
  }

  .vidBox img {
    max-width: 1080px;
    max-height: 100%;
    width: 90vw;
  }

  #rightVideo {
    display: none;
  }

  .videoTitle {
    font-size: 3vw;
  }

  .videoSubtitle {
    font-size: 2.5vw;
  }

  #videoButton {
    margin: 140px 0 50px 0;
  }

}

@media screen and (max-width: 640px) {

  .videoTitle {
    font-size: 4vw;
  }

  .videoSubtitle {
    font-size: 3.5vw;
  }

  #videoButton {
    margin: 120px 0 50px 0;
  }

}

</style>